import ReactDom from "react-dom"
import './App.css';
import React from 'react';
import {HashRouter as Router, Switch, Route, HashRouter} from "react-router-dom"
import CreatePost from './Components/CreatePost';
import Home from "./Components/Home"
import EditPost from "./Components/EditPost"

ReactDom.render(
//<Routes />
<HashRouter>
            <Switch>
                <Route path="/blog-post/" exact component = {Home}/>
                <Route path="/blog-post/createNewPost" exact component = {CreatePost}/>
                <Route path="/blog-post/editPost/:postId" exact component = {EditPost}/>
            </Switch>
</HashRouter>

, document.getElementById("root"))